import React, { Component } from "react";

import button from "./button";

export default class Head extends Component {
    getButtons() {
        return [
            {_id: 1, text: "Home"},
            {_id: 2, text: "Photo"},
            {_id: 3, text: "Contact"}
        ]
    }

    renderButton() {
        return this.getButtons().map((e) => {
            button
        })
    }

}